	module.exports = {"data":{"markdownRemark":{"html":"<p>有人拿来一张黑白点密密麻麻的纸，说是一张三维画，只要盯着看，一会就能看到一幅立体的图画。我看了半天，没有看出来。后来看出来了，才发现开始看的时候，头脑太清醒，这种图画要让视线模糊，然后果然有了图画，而且是立体的，像是透明的玻璃做的一样。真是神奇。不知是谁发明的，发明者真是不容易。</p>\n<p>人往往会迷惑的，越集中精力越迷惑，有时散漫一些，反而不会糊涂。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0086.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0086.md","frontmatter":{"title":"5月5日 星期四"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0086.md absPath of file >>> MarkdownRemark"}}

